* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.home,
.services,
.products,
.contact-us,
.business-card,
.testimonials,
.call-to-aciton,
.sign-up {
  display: flex;
  height: 100vh;
  align-items:start;
  justify-content: center;
  font-size: 3rem;
  padding-left: 15rem;
  padding-right: 15rem;
}

.home > h1,
.services > h1,
.products > h1,
.contact-us > h1,
.business-card > h1,
.testimonials > h1,
.call-to-aciton > h1,
.sign-up > h1 {
  color: #fff;
  font-size: 100px;
}

.services {
  background-image: url('../public/Images/img-2.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.products {
  background-image: url('../public/Images/img-1.jpg');
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.business-card {
  /* background-image: linear-gradient(rgba(211, 211, 211, 0.80), rgba(0, 0, 0, 0.60)), url('../public/Images/img-14.jpg'); */
  background-color:#F8F8F8;
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: gray;
  font-size: 100px;
  height: unset;
}

.testimonials {
  /* background-image: linear-gradient(rgba(0, 0, 0, 0.60), rgba(211, 211, 211, 0.60)), url('../public/Images/img-15.jpg'); */
  background-color: #fff;
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: gray;
  font-size: 100px;
  height: unset;
}

.call-to-action {
  /* background-image: url('../public/Images/img-6.jpg'); */
  background-color:#F8F8F8;
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: gray;
  font-size: 100px;
  height: unset;
}

.contact-us {
  background-image: url('../public/Images/img-3.jpg');
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.sign-up {
  background-image: url('../public/Images/img-8.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.text__md {
  font-size: 46px;
}

.text__sm{
  font-size: 22px;
}

.text__xs{
  font-size: 18px;
}


.m-t-l
{
  margin-top: 4rem;
}

.m-t-m
{
  margin-top: 2rem;
}

.m-t-s
{
  margin-top: 1rem;
}


@media only screen and (max-width: 1024px) {
  .home,
  .services,
  .products,
  .contact-us,
  .business-card,
  .testimonials,
  .call-to-aciton,
  .sign-up {
    padding-left: 1rem;
    padding-right: 1rem;
  }
